import { createSlice } from '@reduxjs/toolkit';

import { objectSelector as _get } from '../../utils';

const initialState = {
  addresses: [],
  addressId: null,
  agreement: false,
  basket: {},
  canGuestPurchase: false,
  cardHolder: '',
  cardCVV: '',
  cardMonth: '',
  cardNumber: '',
  cardYear: '',
  cardType: {},
  country: {},
  currency: 'TL',
  currentTab: 'address',
  billingId: null,
  deliveryOptionId: null,
  deliveryOptions: [],
  errors: null,
  fundsTransferAccounts: [],
  fundsTransferAccountId: null,
  giftable: false,
  giftNote: null,
  hasCampaigns: false,
  installmentId: null,
  installmentMessages: [],
  installments: [],
  isReady: false,
  pending: false,
  paymentOptions: [],
  paymentOptionId: null,
  phoneNumber: null,
  sameBillAddress: true,
  shippingOptions: [],
  shippingId: null,
  shippingAmount: '',
  totalAmount: '',
  useThreeD: false,
}

const checkoutSlice = createSlice({
  name: 'checkout',
  initialState,
  reducers: {
    setReady(state) {
      state.isReady = true;
    },
    setTab(state, { payload }) {
      state.currentTab = payload;
    },
    setCardHolder(state, { payload }) {
      state.cardHolder = payload;
    },
    setCardCVV(state, { payload }) {
      state.cardCVV = payload;
    },
    setCardMonth(state, { payload }) {
      state.cardMonth = payload;
    },
    setCardNumber(state, { payload }) {
      state.cardNumber = payload;
    },
    setCardYear(state, { payload }) {
      state.cardYear = payload;
    },
    setAgreement(state, { payload }) {
      state.agreement = payload;
    },
    setThreeD(state, { payload }) {
      state.useThreeD = payload;
    },
    setSameBillAddress(state, { payload }) {
      state.sameBillAddress = payload;
    },
    setShippingAddressId(state, { payload }) {
      state.addressId = payload;
    },
    setBillingAddressId(state, { payload }) {
      state.billingId = payload;
    },
    setAddresses(state, { payload }) {
      state.addresses = payload;
    },
    setFundsTransferAccountId(state, { payload }) {
      state.fundsTransferAccountId = payload;
    },
    setIndexPageContext(state, { payload }) {
      state.canGuestPurchase = _get(payload, 'can_guest_purchase', initialState.canGuestPurchase);
      state.giftable = _get(payload, 'has_gift_box', initialState.giftable);
    },
    setDeliveryOptionSelectionPageContext(state, { payload }) {
      state.deliveryOptions = _get(payload, 'delivery_options', initialState.deliveryOptions);
    },
    setAddressSelectionPageContext(state, { payload }) {
      state.addresses = _get(payload, 'addresses', initialState.addresses);
      state.country = _get(payload, 'country', initialState.country);
    },
    setShippingOptionSelectionPageContext(state, { payload }) {
      state.shippingOptions = _get(payload, 'shipping_options', initialState.shippingOptions);
    },
    setPaymentOptionSelectionPageContext(state, { payload }) {
      state.paymentOptions = _get(payload, 'payment_options', initialState.paymentOptions);
    },
    setCreditCardConfirmationPageContext(state, { payload }) {
      state.hasCampaigns = _get(payload, 'has_campaigns', initialState.hasCampaigns);
    },
    setInstallmentSelectionPageContext(state, { payload }) {
      state.installments = _get(payload, 'installments', initialState.installments);
      state.installmentMessages = _get(payload, 'installment_messages', initialState.installmentMessages);
      state.cardType = _get(payload, 'card_type', initialState.cardType);
    },
    setFundsTransferChoicePageContext(state, { payload }) {
      state.fundsTransferAccounts = _get(payload, 'bank_accounts', initialState.fundsTransferAccounts);
    },
    clearErrors(state) {
      state.errors = null;
    },
    setPending(state, { payload }) {
      state.pending = payload;
    },
    setErrors(state, { payload }) {
      state.errors = payload;
    },
    setPreOrder(state, { payload }) {
      state.basket = _get(payload, 'basket', initialState.basket);
      state.addressId = _get(payload, 'shipping_address.pk', initialState.addressId);
      state.billingId = _get(payload, 'billing_address.pk', initialState.billingAddress);
      state.currency = _get(payload, 'currency_type_label', initialState.currency);
      state.deliveryOptionId = _get(payload, 'delivery_option.pk', initialState.deliveryOptionId);
      state.installmentId = _get(payload, 'installment.pk', initialState.installmentId);
      state.paymentOptionId = _get(payload, 'payment_option.pk', initialState.paymentOptionId);
      state.shippingId = _get(payload, 'shipping_option.pk', initialState.shippingOptionId);
      state.phoneNumber = _get(payload, 'phone_number', initialState.phoneNumber) || _get(payload, 'shipping_address.phone_number', initialState.phoneNumber);
      state.giftNote = _get(payload, 'gift_box.note', initialState.giftNote);
      state.shippingAmount = _get(payload, 'shipping_amount', initialState.shippingAmount);
      state.totalAmount = _get(payload, 'total_amount', initialState.totalAmount);
    }
  }
});

export const {
  setReady,
  setTab,
  setCardHolder,
  setCardCVV,
  setCardMonth,
  setCardNumber,
  setCardYear,
  setAgreement,
  setThreeD,
  setSameBillAddress,
  clearErrors,
  setErrors,
  setPending,
  setPreOrder,
  setFundsTransferAccountId,
  setShippingAddressId,
  setBillingAddressId,
  setAddresses
} = checkoutSlice.actions;

export const actions = checkoutSlice.actions;

export default checkoutSlice.reducer;
