import ApiClient from '../../api/client';

import fetcher from './fetcher';

import { objectSelector as _get } from '../../utils';

import { getValue } from '../connectSelector';
import {
  setReady,
  setPending,
  setSameBillAddress,
  setFundsTransferAccountId,
} from './reducer';

import {
  selectSameBillAddress,
  selectCurrentBillingId,
  selectCurrentAddressId,
  selectAgreement,
  selectCardNumber,
  selectCardMonth,
  selectCardYear,
  selectCardCVV,
  selectCardHolder,
  selectUseThreeD,
} from './selectors';

export const fetchCheckout = () => async dispatch => {
  await fetcher(ApiClient.checkout.fetchCheckout());

  const addressId = getValue(selectCurrentAddressId);
  const billingId = getValue(selectCurrentBillingId);
  const isSameAddress = addressId === billingId;

  if (addressId && billingId && !isSameAddress) {
    dispatch(setSameBillAddress(isSameAddress));
  }
  dispatch(setReady());
};

export const fetchAddresses = () => () => {
  fetcher(ApiClient.checkout.fetchAddresses());
};

export const setDeliveryOption = (pk) => () => {
  fetcher(ApiClient.checkout.setDeliveryOption(pk));
};

export const setAddress = (pk) => () => {
  const sameAddress = getValue(selectSameBillAddress);
  let billAddressPk = sameAddress ? pk : getValue(selectCurrentBillingId);

  fetcher(ApiClient.checkout.setAddress(pk, billAddressPk));
};

export const setBillingAddress = (pk) => () => {
  const shippingAddress = getValue(selectCurrentAddressId);

  fetcher(ApiClient.checkout.setAddress(shippingAddress, pk));
};

export const setShippingOption = (pk) => () => {
  fetcher(ApiClient.checkout.setShippingOption(pk));
};

export const setPaymentOption = (pk) => () => {
  fetcher(ApiClient.checkout.setPaymentOption(pk));
};

export const setBinNumber = (bin) => () => {
  fetcher(ApiClient.checkout.setBinNumber(bin));
};

export const setInstallment = (pk) => () => {
  fetcher(ApiClient.checkout.setInstallment(pk));
};

export const setSameBillingAddress = (isSame) => (dispatch) => {
  dispatch(setSameBillAddress(isSame));

  if (isSame) {
    const billingId = getValue(selectCurrentBillingId);
    const shippingId = getValue(selectCurrentAddressId);

    if (billingId !== shippingId) {
      dispatch(setAddress(shippingId));
    }
  }
};

export const completeCheckout = () => async (dispatch) => {
  const data = {
    agreement: getValue(selectAgreement),
    card_holder: getValue(selectCardHolder),
    card_number: getValue(selectCardNumber),
    card_month: getValue(selectCardMonth),
    card_year: getValue(selectCardYear),
    card_cvv: getValue(selectCardCVV),
    use_three_d: getValue(selectUseThreeD),
  };

  dispatch(setPending(true));

  await fetcher(ApiClient.checkout.completeCheckout(data));

  dispatch(setPending(false));
};

export const setFundsTransferAccount = (pk) => (dispatch) => {
  dispatch(setFundsTransferAccountId(pk));

  fetcher(ApiClient.checkout.setFundsTransferAccount(pk));
};

export const completeFundsTransfer = () => async (dispatch) => {
  const data = {
    agreement: getValue(selectAgreement),
  };

  dispatch(setPending(true));

  await fetcher(ApiClient.checkout.completeFundsTransfer(data));

  dispatch(setPending(false));
};
