import apiClient from '../client';
import $U from '../urls';

export const fetchCheckout = () => {
  return apiClient.request('GET', $U.CHECKOUT_BASE, {
    format: 'json',
  });
};

export const fetchAddresses = () => {
  return apiClient.request('GET', $U.CHECKOUT_BASE, {
    page: 'AddressSelectionPage',
    format: 'json',
  });
};

export const setDeliveryOption = (optionPk, clear = false) => {
  return apiClient.request(
    'POST',
    `${$U.CHECKOUT_BASE}?page=DeliveryOptionSelectionPage`,
    { delivery_option: optionPk, clear },
  );
};

export const setAddress = (shippingPk, billingPk) => {
  return apiClient.request(
    'POST',
    `${$U.CHECKOUT_BASE}?page=AddressSelectionPage`,
    { shipping_address: shippingPk, billing_address: billingPk },
  );
}

export const setShippingOption = (optionPk) => {
  return apiClient.request(
    'POST',
    `${$U.CHECKOUT_BASE}?page=ShippingOptionSelectionPage`,
    { shipping_option: optionPk },
  );
};

export const setPaymentOption = (optionPk) => {
  return apiClient.request(
    'POST',
    `${$U.CHECKOUT_BASE}?page=PaymentOptionSelectionPage`,
    { payment_option: optionPk },
  );
};

export const setBinNumber = (binNumber) => {
  return apiClient.request(
    'POST',
    `${$U.CHECKOUT_BASE}?page=BinNumberPage`,
    { bin_number: binNumber },
  );
};

export const setInstallment = (installmentPk) => {
  return apiClient.request(
    'POST',
    `${$U.CHECKOUT_BASE}?page=InstallmentSelectionPage`,
    { installment: installmentPk },
  )
};

export const completeCheckout = (checkoutData) => {
  return apiClient.request(
    'POST',
    $U.CHECKOUT_BASE,
    checkoutData,
  );
};

export const setFundsTransferAccount = (accountPk) => {
  return apiClient.request(
    'POST',
    `${$U.CHECKOUT_BASE}?page=FundsTransferChoicePage`,
    { bank_account: accountPk },
  );
};

export const completeFundsTransfer = (fundsTransferData) => {
  return apiClient.request(
    'POST',
    `${$U.CHECKOUT_BASE}?page=FundsTransferPage`,
    fundsTransferData, 
  );
};
