import URL from 'shop-packages/api/urls';

import ActionForm, {displayErrors} from '../../components/form-action';
import {ApiClient} from "shop-packages";


const subscriptionForm = $('.js-subscription-form');
const form = ActionForm(subscriptionForm);
const modal = $('.js-subs_modal');

const footer = {

  footerTitleClick: function () {
    const footerTitle = $('.js-footer-title');
    footerTitle.on('click', function () {
      $(this).toggleClass('active');
    })
  },

  formInitialize: function(){

    form.action = ApiClient.misc.subscription;

    form.beforeAction = () => {
      form.actionParams = [
        subscriptionForm.serialize(),
      ];
    };

    form.onSuccess = (response) => {
      footer.onSuccess(response);
    };

    form.onError = err => {
      footer.onError(err);
    };

  },

  onSuccess : function(response) {
    if (response.data && response.data.errors) {
      displayErrors(response.data.errors, form);
      return;
    }

    modal.removeClass('d-none');
    $('html,body').addClass('overflow-hidden')
  },

  onError : function(response) {
    console.log(response)
  },

  modalAction: function(){
    modal.on('click',function () {
      $(this).addClass('d-none');
      $('html,body').removeClass('overflow-hidden')
    })
  },

  init: function(){
    footer.modalAction();
    footer.formInitialize();
    footer.footerTitleClick();
  },

};

export default function () {
  footer.init()
};

