export default class Flatpage {

  constructor() {
    this.init();
  }

  init(){
    this.catalogSlider = $('.js-catalog-slider');
    this.collectionSlider = $('.js-collection-slider');

    let catalogSliderProp = {
      item: 4,
      loop: true,
      pager: false,
      galleryMargin: 20,
      autoWidth: false,
      controls: true,
      responsive: [
        {
          breakpoint: 991,
          settings: {
            item: 3
          }
        },
        {
          breakpoint: 768,
          settings: {
            item: 2
          }
        },
        {
          breakpoint: 575,
          settings: {
            item: 2
          }
        },
      ]
    }

    let collectionSliderProp = {
      item: 3,
      loop: true,
      pager: false,
      galleryMargin: 30,
      autoWidth: false,
      controls: false,
      responsive: [
        {
          breakpoint: 991,
          settings: {
            item: 4
          }
        },
        {
          breakpoint: 768,
          settings: {
            item: 4,
            autoWidth:true
          }
        },
        {
          breakpoint: 575,
          settings: {
            item: 2,
            autoWidth:false,
          }
        },
      ]
    };

    this.initSidebar();
    this.initSlider(this.catalogSlider , catalogSliderProp);
    this.initSlider(this.collectionSlider , collectionSliderProp)
  }

  initSidebar(){
    if($('.js-sidebar-menu').length){
      const path = window.location.pathname + window.location.search ;
      $('.js-sidebar-menu li').each(function(){
        const self = $(this);
        const href = self.children().attr('href');
        if( href === path){
          self.addClass('active');
        }
      })
    }
  }

  initSlider(selector,properties){
    if (!selector && !selector.length) return;
    selector.lightSlider({...properties});
  }

}