import { ApiClient } from 'shop-packages';

import accordion from '../components/accordion';
import * as actionTypes from "shop-packages/analytics/action-types";
import {pushEvent} from "shop-packages/analytics";
import { mobile } from '../utils';

export default class Listing {
  constructor() {
    this.container = $(document.getElementById('ListPage'));
    this.$filterText = this.container.find('.js-filter-input-text');
    this.selectedFilters = [];
    this.selectedSorter = '';
    this.selectedGrid = '';
    this.page = 0;
    this.init();

    window.onpopstate = history.onpushstate = () => {
      this.fetch(location.pathname+location.search);
    }
  }

  init() {
    this.$filterText.text().trim() === '' && this.$filterText.text('Filtreler');
    this.productsWrapper = $(document.getElementById('ListProductWrapper'));
    this.listProductFavouriteElem = $('.js-product-item-favourite-elem');
    this.listProductFavouriteGuestElem = $('.js-product-item-favourite-guest');
    $('body').removeClass('overflow-hidden');

    this.listProductFavouriteElem.on('click', this.listFavouriteAction.bind(this));
    this.listProductFavouriteGuestElem.on('click', this.listFavouriteGuestAction.bind(this));
    this.listFavouriteGetFavouritePk();

    accordion();

    this.getSelected();

    this.container.find('.js-product-wrapper').click(e => {
      const productPk = $(e.currentTarget).data("pk");
      this.pushAnalyticsData( productPk , actionTypes.productClicked);
    });

    this.$sorterMobile = $(document.getElementById('ListSorterMobileContainer'));
    $(document.getElementById('ListSorterMobileButton')).click(() => {
      this.showMobileSorter();
    });

    $(document.getElementById('ListSorterMobileCloser')).click(() => {
      this.hideMobileSorter();
    });

    $(document.getElementById('ListFiltersClearButton')).click(() => {
      this.clearFilters();
    });

    this.$filtersMobileContainer = $(document.getElementById('ListFacetsMobileContainer'));
    this.$filtersMobileForm = $(document.getElementById('ListFacetsForm'));
    this.$filtersMobileForm.submit(this.applyFilters.bind(this));

    $(document.getElementById('ListFiltersForm')).submit(this.applyFilters.bind(this));

    $(document.getElementById('ListFacetsButton')).click(() => {
      this.showMobileFilters();
    });

    $(document.getElementById('ListFacetsApplyButton')).click(() => {
      this.$filtersMobileForm.submit();
    });

    $(document.getElementById('ListFacetsMobileCloser')).click(() => {
      this.hideMobileFilters();
    });

    $('.dropdown-menu').on('click.bs.dropdown', (e) => {
      e.stopPropagation();
    });

    this.container.find('.js-category-choice').click(e => {
      e.preventDefault();

      const $el = $(e.currentTarget);
      const href = $el.attr('href');
      this.page = 0;
      history.pushState(null, document.title, href);
      this.fetch(href);
    });

    this.container.find('.js-pagination-item').click(e => {
      e.preventDefault();

      const $el = $(e.currentTarget);

      if ($el.hasClass('passive') || $el.hasClass('active')) {
        return;
      }

      const page = parseInt($el.text(), 10);

      if (this.page !== page) {
        this.page = page;
        const href = $el.attr('href');
        history.pushState(null, document.title, href);
        this.fetch(href);
      }
    });

    this.container.find('.js-sorter').change(e => {
      const value = $(e.currentTarget).val();

      if (value === 'default') {
        this.selectedSorter = '';
      } else {
        this.selectedSorter = value;
      }

      this.page = 0;
      this.fetchProducts();

      this.hideMobileSorter();
    });


    this.selectedGrid = this.container.find('.js-grid-item.active').attr('data-grid');

    this.onClickReadMore();
  }

  async listFavouriteAction(e){
    e.preventDefault();
    e.stopPropagation();

    const productPk = $(e.target).attr('data-product-pk');
    let removePk = $(e.target).attr('data-favourite-pk');
    const isActive = $(e.target).hasClass('active');

    try{
      if (isActive) {
        await ApiClient.favorites.removeFavourite(removePk)
      }
      else {
        await ApiClient.favorites.addFavourite(productPk);
        
        const response = await ApiClient.favorites.fetchFavouriteStatus([productPk]);
        
        if (response.data.results && response.data.results.length) {
          const favoritePk = response.data.results[0].pk;
          $(e.target).attr('data-favourite-pk', favoritePk);
        }
      }

      $(e.target).toggleClass('active');
    }
    catch(e){
      console.log(e);
    }
  }

  listFavouriteGuestAction(e){
    e.preventDefault();
    e.stopPropagation();

    let url = $(e.target).attr('data-url');

    window.location.href = url;
  }

  async listFavouriteGetFavouritePk(){
    const productPk = this.listProductFavouriteElem.attr('data-product-pk');
    const limit = $('.js-product-wrapper').length;
    const response = await ApiClient.favorites.fetchFavourites(limit);

    if (response.data.results && response.data.results.length) {
      response.data.results.map(response_item => {
        $(this.listProductFavouriteElem).each(function(index, item){
          var productPk = $(item).attr('data-product-pk');
          
          if(response_item.product.pk == productPk){
            $(item).attr('data-favourite-pk', response_item.pk);
            $(item).addClass('active');
          }
        })
      })
    }
  }

  onClickReadMore(){
    let $listDescriptionWrapper = this.container.find('.js-list-description-text-wrapper');
    if($($listDescriptionWrapper).length > 0){
      let $maxLength = 475;
      let $fullContent = $($listDescriptionWrapper).html();
      if($fullContent.length>$maxLength) {
        let short_content =  $($fullContent).html().substr( 0, $maxLength );
        let long_content = $($fullContent).html();
        let short_html = `<span class="short_content">${short_content}</span>...&nbsp;&nbsp;<a href="javascript:;" class="js-read-more read-more">Devamını Oku</a>`;
        let long_html = `<span class="short_content">${long_content}</span><a href="javascript:;" class="js-read-more read-more">Devamını Gizle</a>`;
        $listDescriptionWrapper.html(short_html);
        let $readMore = this.container.find('.js-read-more');
  
        $(document).on('click', $readMore,() => {
          if($listDescriptionWrapper.hasClass('show')) {
            $listDescriptionWrapper.html(short_html);
          }
          else $listDescriptionWrapper.html(long_html); {
            $listDescriptionWrapper.toggleClass('show');
          }
        })
      }
    }
    
  }
  showMobileSorter() {
    $('html, body').stop().scrollTop(0);
    this.$sorterMobile.show();
    $('body').addClass('overflow-hidden');
  }

  hideMobileSorter() {
    this.$sorterMobile.hide();
    $('body').removeClass('overflow-hidden');
  }
  
  showMobileFilters() {
    $('html, body').stop().scrollTop(0);
    this.$filtersMobileContainer.show();
    $('body').addClass('overflow-hidden');
  }

  hideMobileFilters() {
    this.$filtersMobileContainer.hide();
    this.$filtersMobileForm.trigger('reset');
    $('body').removeClass('overflow-hidden');
  }

  addFilter(name, value) {
    this.selectedFilters.push({ name, value });
    this.page = 0;
    this.fetchProducts();
  }


  pushAnalyticsData(productPk , actionType){
    $('.analytics-data').each(function() {
      const analyticsData = JSON.parse($(this).text());
      if (!analyticsData.type) return;
      if (analyticsData.type === actionTypes.productListViewed){
        const products = analyticsData.payload;
        $.each( products , function(index , value){
          for(const key in value){
            if (productPk == value[key]){
              const analyticsData = {
                type : actionType,
                payload : {
                  products : [value]
                }
              };
              pushEvent(analyticsData);
              return;
            }
          }
        })
      }
    });
  }


  removeFilter(name, value) {
    this.selectedFilters = this.selectedFilters.filter(selectedFilter => {
      return !(selectedFilter.name === name && selectedFilter.value === value)
    });
    this.page = 0;
    this.fetchProducts();
  }

  getSelected() {
    this.selectedFilters = [];
    this.selectedSorter = '';

    this.container.find('.js-filter-choice:checked').each((_, el) => {
      const $el = $(el);
      $el.parents('.accordion-item').addClass('active');
      const name = $el.attr('name');
      const value = $el.val();

      this.selectedFilters.push({ name, value });
    });

    const sorterValue = this.container.find('.js-sorter').val();
    if (sorterValue !== 'default') {
      this.selectedSorter = sorterValue;
    }
  }

  clearFilters() {
    this.selectedFilters = [];
    this.page = 0;
    this.fetchProducts();
  }

  applyFilters(e) {
    e.preventDefault();
    const form = $(e.target);

    this.selectedFilters = form.serializeArray();
    this.page = 0;
    this.fetchProducts();
  }

  fetchProducts() {
    let params = [];

    if (location.pathname === '/list/') {
      const selectedCategory = this.container.find('.js-category-choice[data-checked=true]');

      if (selectedCategory.length) {
        const categoryData = selectedCategory.first().data();
        params.push(
          $.param({[categoryData.name]: categoryData.value})
        )
      }
    }

    for (let filter of this.selectedFilters) {
      params.push(
        $.param({[filter.name]: filter.value})
      );
    }

    if (this.selectedSorter) {
      params.push(
        $.param({sorter: this.selectedSorter})
      );
    }

    if (this.selectedGrid) {
      params.push(
        $.param({grid: this.selectedGrid})
      );
    }

    if (this.page && this.page !== 1) {
      params.push(
        $.param({page: this.page})
      );
    }

    const query = !params.length ?
      location.pathname :
      `${location.pathname}?${params.join('&')}`;

    history.pushState(null, document.title, query);
    this.fetch(query);
  }

  async fetch(query) {
    const response = await ApiClient.misc.fetchPageHtml(query);

    const result = $(response).find('#ListPage').html();
    this.container.html(result);
    this.init();

    $('html, body').stop().animate({ scrollTop: mobile() ? this.productsWrapper.offset().top : this.container.offset().top });
  }
}