import 'bootstrap';
import 'lightslider';
import 'select2';

import { ApiClient } from 'shop-packages';
import store from "shop-packages/redux/store";
import { fetchBasket } from "shop-packages/redux/basket/actions";

import './index.scss';
import '../assets/img/favicon.png';
