import Component from "../../components/dom/component";
import ActionButton from "../../components/button-action";
import { basketErrorSelector, basketReducer } from "shop-packages/redux/basket/selectors";
import observe from "shop-packages/redux/connectSelector";
import { removeDiscount, setDiscount } from "shop-packages/redux/basket/actions";
import { store } from "shop-packages";

export default class VoucherForm extends Component {
  static selector = '.js-basket-voucher';

  constructor(_$) {
    super(_$, VoucherForm.selector);

    this.$input = this._$.find('[name="code"]');
    this.$error = this._$.find('#jsVoucherFormError');
    this.$submitButton = this._$.find('.js-voucher-submit-button');
    this.$removeButton = this._$.find('.js-voucher-remove-button');
    this.$form = this._$.find('.js-basket-voucher-form');
    this.$openFormButton = this._$.find('.js-basket-voucher-form-open-button');


    this.$openFormButton.on('click', () => {
      this.$form.show();
      this.$openFormButton.hide()
    });

    this.submitButton = new ActionButton(this.$submitButton);
    this.submitButton.action = (...args) => store.dispatch(setDiscount(...args));
    this.submitButton.beforeAction = () => {
      this.submitButton.actionParams = [
        this.$input.val().trim()
      ]
    };

    this.removeButton = new ActionButton(this.$removeButton);
    this.removeButton.action = () => store.dispatch(removeDiscount());


    observe(basketReducer).subscribe((state) => {
      const { basket: { voucher_code } } = state;
      if (voucher_code) {
        this.$submitButton.hide();
        this.$removeButton.show();
      } else {
        this.$submitButton.show();
        this.$removeButton.hide();
      }
    });

    observe(basketErrorSelector).subscribe(({ errors }) => {
      const err = errors.find(e => (((e || {}).error || {}).error_code || "").includes("basket_voucher_100_"));
      if (err) {
        this.$error.html(err.error.non_field_errors).show()
      } else {
        this.$error.html('').hide()
      }
    })
  }
}
