import { getValue } from 'shop-packages/redux/connectSelector';
import {
  selectCurrentAddress,
  selectCurrentBillingAddress,
  selectCurrentPaymentOption,
  selectShippingAmount,
  selectCurrency,
} from 'shop-packages/redux/checkout/selectors';

class RemoteSalesAgreementModal {
  constructor() {
    this.$modal = $(document.getElementById('salesModal'));

    this.$modal.on('show.bs.modal', this.fillModal.bind(this));
  }

  fillModal() {
    const $customerName = this.$modal.find('#SalesCustomerName');
    const $customerAddress = this.$modal.find('#SalesCustomerAddress');
    const $customerPhone = this.$modal.find('#SalesCustomerPhone');
    const $customerEmail = this.$modal.find('#SalesCustomerEmail');
    const $paymentType = this.$modal.find('#SalesPaymentType');
    const $shippingAddress = this.$modal.find('#SalesShippingAddress');
    const $shippingPerson = this.$modal.find('#SalesShippingPerson');
    const $billingAddress = this.$modal.find('#SalesBillingAddress');
    const $shippingAmount = this.$modal.find('#SalesShippingAmount');

    const shippingAddress = getValue(selectCurrentAddress);
    const billingAddress = getValue(selectCurrentBillingAddress);
    const paymentOption = getValue(selectCurrentPaymentOption);
    const shippingAmount = getValue(selectShippingAmount);
    const currency = getValue(selectCurrency);

    $customerName.html(
      billingAddress.is_corporate ?
        billingAddress.company_name : 
        `${billingAddress.first_name} ${billingAddress.last_name}`
    );

    $customerAddress.html(`${billingAddress.line} ${billingAddress.township.name} ${billingAddress.postcode} ${billingAddress.city.name}`);
    $customerPhone.html(`${billingAddress.phone_number}`);
    $customerEmail.html(`${billingAddress.email}`);

    $paymentType.html(`${paymentOption.payment_type_label}`);

    $shippingAddress.html(`${shippingAddress.line} ${shippingAddress.township.name} ${shippingAddress.postcode} ${shippingAddress.city.name}`);
    $shippingPerson.html(
      shippingAddress.is_corporate ?
        shippingAddress.company_name : 
        `${shippingAddress.first_name} ${shippingAddress.last_name}`
    );

    $billingAddress.html(`${billingAddress.line} ${billingAddress.township.name} ${billingAddress.postcode} ${billingAddress.city.name}`);
    $shippingAmount.html(`${shippingAmount} ${currency}`);
  }
}

export default RemoteSalesAgreementModal;