export default class OrdersDetail{
  constructor(){
    this.$container = $(document.getElementById('orderDetailWrapper'));
    this.orderID = this.$container.attr('data-id');
    this.$form = $(document.getElementById('orderDetailActionForm'));
    this.$select = $('select[name=orderDetailSelect]');

    this.$form.submit(this.onFormSubmit.bind(this));
  }

  onFormSubmit(e) {
    e.preventDefault();
    const selectValue = $(this.$select).val();

    if (selectValue == 'cancellation') {
      window.location.pathname = `/users/orders/${this.orderID}/cancellation`;
    }
    else if(selectValue == 'contact'){
      window.location.pathname = `/account/contact/`;
    }
  }
}