import store from '../store';
import { getValue } from '../connectSelector';

import {
  selectCurrentDeliveryOptionId,
  selectCurrentAddressId,
  selectCurrentShippingId,
  selectCurrentPaymentOptionId,
  selectCurrentInstallmentId,
  selectCurrentFundsTransferAccountId,
} from './selectors';

import {
  setDeliveryOption,
  setAddress,
  setShippingOption,
  setPaymentOption,
  setInstallment,
  setFundsTransferAccount,
} from './actions';

export const DeliveryOptionSelectionPage = context => {
  const hasDeliveryOption = getValue(selectCurrentDeliveryOptionId);
  if (hasDeliveryOption) {
    return;
  }

  const { delivery_options: deliveryOpts } = context;
  if (!deliveryOpts || !deliveryOpts.length) {
    return;
  }

  const defaultDelivery = deliveryOpts.find(option => option.delivery_option_type === 'customer');
  const deliveryPk = defaultDelivery ? defaultDelivery.pk : deliveryOpts[0].pk;

  store.dispatch(setDeliveryOption(deliveryPk));
};

export const AddressSelectionPage = context => {
  const { addresses } = context;

  if (addresses && addresses.length) {
    const selectedAddress = getValue(selectCurrentAddressId);
    if (selectedAddress && addresses.find(({pk}) => selectedAddress === pk )) {
      return;
    }
  
    const primaryAddress = addresses.find(address => !!address.primary);

    if (primaryAddress) {
      store.dispatch(setAddress(primaryAddress.pk));
      return;
    }

    store.dispatch(setAddress(addresses[0].pk));
  }
};

export const ShippingOptionSelectionPage = context => {
  const hasSelectedShipping = getValue(selectCurrentShippingId);
  if (hasSelectedShipping) {
    return;
  }

  const { shipping_options: shippingOpts } = context;
  if (shippingOpts && shippingOpts.length) {
    store.dispatch(setShippingOption(shippingOpts[0].pk));
  }
};

export const PaymentOptionSelectionPage = context => {
  const hasSelectedPayment = getValue(selectCurrentPaymentOptionId);
  if (hasSelectedPayment) {
    return;
  }

  const { payment_options: paymentOpts } = context;
  if (!paymentOpts || !paymentOpts.length) {
    return;
  }

  const storedCardOption = paymentOpts.find(option => option.payment_type === 'stored_card');
  const paymentPk = storedCardOption ? storedCardOption.pk : paymentOpts[0].pk;

  store.dispatch(setPaymentOption(paymentPk));
};

export const InstallmentSelectionPage = context => {
  const hasSelectedInstallment = getValue(selectCurrentInstallmentId);
  if (hasSelectedInstallment) {
    return;
  }

  const { installments } = context;
  if (installments && installments.length) {
    store.dispatch(setInstallment(installments[0].pk));
  }
};

export const CreditCardThreeDSecurePage = context => {
  const { redirect_url } = context;
  if (redirect_url) {
    document.location.href = redirect_url;
  }
};

export const FundsTransferChoicePage = context => {
  const { bank_accounts } = context;
  if (bank_accounts && bank_accounts.length) {
    store.dispatch(setFundsTransferAccount(bank_accounts[0].pk));
  }
}

export const ThankYouPage = context => {
  if (context.redirect_url) {
    window.location.replace(context.redirect_url);
  }
};
