import { createSelector } from '@reduxjs/toolkit';

import { objectSelector as _get } from '../../utils';

const checkoutSelector = state => state.checkout;

export const selectIsReady = createSelector(
  checkoutSelector,
  checkout => checkout.isReady
);

export const selectTab = createSelector(
  checkoutSelector,
  checkout => checkout.currentTab
);

export const selectErrors = createSelector(
  checkoutSelector,
  checkout => checkout.errors
);

export const selectPending = createSelector(
  checkoutSelector,
  checkout => checkout.pending
);

export const selectDeliveryOptions = createSelector(
  checkoutSelector,
  checkout => checkout.deliveryOptions
);

export const selectCurrentDeliveryOptionId = createSelector(
  checkoutSelector,
  checkout => checkout.deliveryOptionId
);

export const selectAddresses = createSelector(
  checkoutSelector,
  checkout => checkout.addresses
);

export const selectCurrentAddressId = createSelector(
  checkoutSelector,
  checkout => checkout.addressId
);

export const selectCurrentAddress = createSelector(
  [selectAddresses, selectCurrentAddressId],
  (addresses, currentId) => addresses.find(({pk}) => pk === currentId)
);

export const selectCurrentAddressCity = createSelector(
  selectCurrentAddress,
  address => _get(address, 'city.name', '')
);

export const selectCurrentBillingId = createSelector(
  checkoutSelector,
  checkout => checkout.billingId
);

export const selectCurrentBillingAddress = createSelector(
  [selectAddresses, selectCurrentBillingId],
  (addresses, currentId) => addresses.find(({ pk }) => pk === currentId)
);

export const selectShippingOptions = createSelector(
  [checkoutSelector, selectAddresses],
  (checkout, addresses) => addresses.length ? checkout.shippingOptions : []
);

export const selectCurrentShippingId = createSelector(
  checkoutSelector,
  checkout => checkout.shippingId
);

export const selectCurrentShipping = createSelector(
  [selectShippingOptions, selectCurrentShippingId],
  (shippingOptions, currentId) => shippingOptions.find(({ pk }) => pk === currentId)
);

export const selectShippingAmount = createSelector(
  checkoutSelector,
  checkout => checkout.shippingAmount
);

export const selectTotalAmount = createSelector(
  checkoutSelector,
  checkout => checkout.totalAmount
);

export const selectPreOrderBasket = createSelector(
  checkoutSelector,
  checkout => checkout.basket
);

export const selectSummaryData = createSelector(
  [selectShippingAmount, selectTotalAmount, selectPreOrderBasket],
  (shippingAmount, totalAmount, basket) => {
    return {
      shippingAmount,
      totalAmount,
      basket
    };
  }
);

export const selectAddressAndShippingSet = createSelector(
  [selectCurrentAddress, selectCurrentShipping],
  (address, shipping) => !!address && !!shipping
);

export const selectAddressAndShipping = createSelector(
  [selectCurrentAddress, selectCurrentShipping],
  (address, shipping) => {
    return { address, shipping };
  }
);

export const selectSameBillAddress = createSelector(
  checkoutSelector,
  checkout => checkout.sameBillAddress
);

export const selectCurrency = createSelector(
  checkoutSelector,
  checkout => checkout.currency
);

export const selectPaymentOptions = createSelector(
  checkoutSelector,
  checkout => checkout.paymentOptions
);

export const selectCurrentPaymentOptionId = createSelector(
  checkoutSelector,
  checkout => checkout.paymentOptionId
);

export const selectCurrentPaymentOption = createSelector(
  [selectPaymentOptions, selectCurrentPaymentOptionId],
  (paymentOptions, currentId) => paymentOptions.find(({ pk }) => pk === currentId)
);

export const selectCardHolder = createSelector(
  checkoutSelector,
  checkout => checkout.cardHolder
);

export const selectCardNumber = createSelector(
  checkoutSelector,
  checkout => checkout.cardNumber
);

export const selectCardMonth = createSelector(
  checkoutSelector,
  checkout => checkout.cardMonth
);

export const selectCardYear = createSelector(
  checkoutSelector,
  checkout => checkout.cardYear
);

export const selectCardCVV = createSelector(
  checkoutSelector,
  checkout => checkout.cardCVV
);

export const selectUseThreeD = createSelector(
  checkoutSelector,
  checkout => checkout.useThreeD
);

export const selectBinNumber = createSelector(
  selectCardNumber,
  cardNumber => (cardNumber && cardNumber.length >= 6) ?
    cardNumber.substring(0, 6) : null
);

export const selectCardType = createSelector(
  checkoutSelector,
  checkout => checkout.cardType
);

export const selectCardTypeName = createSelector(
  selectCardType,
  cardType => _get(cardType, 'name', '')
);

export const selectCardTypeImage = createSelector(
  selectCardType,
  cardType => _get(cardType, 'logo', '')
);

export const selectInstallments = createSelector(
  checkoutSelector,
  checkout => checkout.installments
);

export const selectCurrentInstallmentId = createSelector(
  checkoutSelector,
  checkout => checkout.installmentId
);

export const selectAgreement = createSelector(
  checkoutSelector,
  checkout => checkout.agreement
);

export const selectFundsTransferAccounts = createSelector(
  checkoutSelector,
  checkout => checkout.fundsTransferAccounts
);

export const selectCurrentFundsTransferAccountId = createSelector(
  checkoutSelector,
  checkout => checkout.fundsTransferAccountId
);

export const selectCurrentFundsTransferAccount = createSelector(
  [selectFundsTransferAccounts, selectCurrentFundsTransferAccountId],
  (transferAccounts, currentId) => transferAccounts.find(({ pk }) => pk === currentId)
);
