import { ApiClient } from 'shop-packages';

class Gift {
  observers = [];

  constructor() {
    
    this.initObservers();
  }


  giftCard() {

    const $giftElem = $(document.getElementById('GiftButton'));
    
    $giftElem.on('click', async function (e){
      e.preventDefault();

      const isActive = $giftElem.hasClass('active');

      if (isActive) {
        const response = await ApiClient.gift.removeGift();

        if(response && response.status == 'success'){
          $giftElem.html(`
          <i class="fa fa-gift" aria-hidden="true"></i>
          <span>Hediye Paketi Ekle</span>
          `).removeClass('active');
        }
    
      } else {
        const response = await ApiClient.gift.addGift('null');
        
        if(response && response.status == 'success'){
          $giftElem.html(`
            <i class="fa fa-gift" aria-hidden="true"></i>
            <span>Hediye Paketini Kaldır</span>
            <i class="fa fa-times" aria-hidden="true"></i>
          `).addClass('active');
        }

      }
      
    });
  }

  initObservers() {
    
    this.giftCard();
  }
}


export default Gift;